import React from 'react';
import { Link } from 'react-router-dom';

const SignInP = (props) => (
    <div className="login-outer">
        <div className="login-container">
            <h3 className="form-heading">Login to your OSSO Account</h3>
            <div className="form">
                <div className="form-element">
                    <input type="email" placeholder="Username or Email Address" onChange={(e) => { props.setEmail(e.target.value) }} />
                    <div className="errro-msg"></div>
                </div>
                <div className="form-element">
                    <input type="password" placeholder="Enter Password" onChange={(e) => { props.setPass(e.target.value) }} />
                    <div className="errro-msg"></div>
                </div>
            </div>
            <div className="row-section">
                <div className="remember-me">
                    <label className="checkbox">Remember me
                                <input type="checkbox" /><i className="input-helper"></i>
                    </label>
                </div>
                <Link to='/forgot'>Forgot Password?</Link>
            </div>
            <div className="submit-wrap">
                <Link onClick={props.handleClick}>Login</Link>
            </div>
            <p className="redirect-link">Don't Have Account <Link to='/signup'>Sign Up</Link></p>
        </div>
    </div>
);

export default SignInP;