import React ,{ useState }from 'react';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import {ossoApi} from '../../redux/api';
import {Login} from '../../redux/auth/action';
import SignInP from '../../screens/auth/SignInP';
import {
    isValidEmail,
} from '../../Utils/Validation';
function SignIn(props) {
    const [email,setEmail]=useState(null);
    const [pass, setPass] = useState(null);
    const [isLogedIn,setIsLogedIn]=useState(false)
    const handleClick = () => {
        if (!isValidEmail(email))
        {
            alert('Enter valid email')
            return
        }
        if(!(pass))
        {
            alert('Enter password')
            return
        }
        const data = {
            email: email,
            password: pass,
        }
        let dataInput = JSON.stringify(data);
       console.log("emal",dataInput);
        ossoApi.post(`auth/login`,dataInput,{
            headers : {
                'content-type' : 'application/json'
            }
        }).then(res => {
            console.log(res)
            if(res.data && res.data.token && res.data.data) {
                props.Login(res.data.data,res.data.token)
                setIsLogedIn(true)
            }
            else{
                alert('try Again !');
            }
        }).catch(err=>{
            console.log(err)
            alert('Enter Valid Credential')
        })
    }
    if(isLogedIn)
    {
        props.history.push('/');
    }
    return (
        <SignInP
            setEmail={setEmail}
            setPass={setPass}
            handleClick={handleClick}
         />
    );
}

export default connect(null,{Login}) (SignIn) ;
